.register-page {
    background-image: url('../styles/img1.jpeg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.register-form {
    background-color: rgba(255, 255, 255, 0.8);
    padding: 25px;
    border-radius: 12px;
    width: 400px;
    text-align: center;
    opacity: 0;
    animation: zoomOut 0.5s ease forwards;
}

.register-h1 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
}

.form-item {
    margin-bottom: 20px;
}

.ant-form-item-label label {
    font-size: 17px !important; /* Adjust the font size as needed */
}

.input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
}

.login-link {
    text-decoration: none;
    color: #1c1f21;
    font-size: 16px;
}

.login-link:hover {
    text-decoration: underline;
}

.register-button {
    background-color: #333;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s;
}

.register-button:hover {
    background-color: #494a4c;
}

@keyframes zoomOut {
    0% {
        transform: scale(1);
        opacity: 0;
    }
    100% {
        transform: scale(0.95);
        opacity: 1;
    }
}
