.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  
}

.register-h1 {
  font-size: 24px; /* Adjust the font size as needed */
  font-weight: bold;
  margin-bottom: 20px; /* Add some space below the heading */
  text-align: center; /* Center-align the heading */
  color: #333; /* Set the text color */
  text-transform: uppercase; /* Convert text to uppercase */
  letter-spacing: 2px; /* Increase letter spacing for emphasis */
}

/* Example: Add a border-bottom to create an underline effect */
.register-h1::after {
  content: "";
  display: block;
  width: 60px; /* Adjust the width of the underline */
  border-bottom: 2px solid #007bff; /* Choose a color for the underline */
  margin: 10px auto; /* Center the underline and add some space */
}


